import React, { Component } from 'react';
import { connect } from "react-redux";
import { baseUrl } from '../../../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'
import NetworksSkeleton from './NetworksSkeleton';
import { GET_NETWORK_ICON } from '../../../../../../Services/Utils/utils';
class Networks extends Component {
    // constructor() {
    //     super();
    //     //this.state = {showModalType:'addFeed' };
    // }

    uploadType = (uploadType,network) => {
        this.props.uploadTypeCallback(uploadType,network)
    };
    render() {
        const { networkList,selectedNetworkId } = this.props;
        return <PerfectScrollbar>
            <div className="addSourceNetworkList px-3">
                <div className="mb-4">
                    <input type="text" className="form-control" placeholder="Search your network..." />
                </div>
                <button type="button" className="btn btn-active-light border border-dashed py-3 w-100 mb-2 fs-7 fw-medium" onClick={() => this.uploadType("uploadAssets",null)}>
                    <span className="svg-icon svg-icon-2">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M7 20.9811C3.64378 20.7257 1 17.9216 1 14.5C1 12.1716 2.22429 10.1291 4.06426 8.9812C4.56469 5.044 7.92686 2 12 2C16.0731 2 19.4353 5.044 19.9357 8.9812C21.7757 10.1291 23 12.1716 23 14.5C23 17.9216 20.3562 20.7257 17 20.9811V21H7V20.9811ZM13 13H16L12 8L8 13H11V17H13V13Z" fill="currentColor"></path></svg>
                    </span>Browse From Device
                </button>
                {
                    networkList && networkList.length > 0 ? networkList.map((network, index) => {
                        return <button key={`network${network.id}`} type="button" className={`btn w-100 btn-active-light btn-sm mb-1 fs-7 fw-medium text-start d-flex align-items-center ${selectedNetworkId===network.id?'active':''}`} onClick={() => this.uploadType(network.id===29?'onsite-upload':'byNetwork',network)}>
                            <span className="symbol symbol-24 me-2 bg-light">
                                <img className="object-center-contain" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt="" height={30} width={30} />
                            </span>
                            {network.name}
                        </button>
                    }) : <NetworksSkeleton />
                }
                
            </div>
        </PerfectScrollbar>
    }
}


const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData
    }
}
export default connect(mapStateToProps)(Networks);